.slider-btn {
  position: absolute;

  width: 16px;
  height: 32px;
  padding: 0;
  background: transparent;
  border: hidden;
  cursor: pointer;

  &:hover::before,
  &:hover::after {
    background-color: #1c3374;
  }
}

.slider-btn::before,
.slider-btn::after {
  content: "";
  left: -3px;
  display: block;

  width: 25px;
  height: 1px;
  background-color: #c4c4c4;
}

.slider-btn--next {
  &::before {
    position: absolute;
    top: 11px;
    left: -4px;
    transform: rotate(45deg);
  }

  &::after {
    position: absolute;
    top: 29px;
    left: -4px;
    transform: rotate(-45deg);
  }
}

.slider-btn--previous {
  &::before {
    position: absolute;
    top: 29px;
    left: -3px;
    transform: rotate(45deg);
  }

  &::after {
    position: absolute;
    top: 11px;
    left: -3px;
    transform: rotate(-45deg);
  }
}

.slider-btn[type="button"]:disabled {
  pointer-events: none;

  &:hover::before,
  &:hover::after {

    color: #9f9d9d;
  }
}

@media (max-width: 767px) {
  .slider-btn {
    width: 15px;
    height: 23px;

    &::before,
    &::after {
      content: "";
      left: 1px;

      width: 14px;
      height: 1px;
    }

    &--next {
      &::before {
        top: 6px;
        // left: 1px;
      }

      &::after {
        top: 16px;
        // left: 1px;
      }
    }

    &--previous {
      &::before {
        top: 16px;
        // left: 1px;
      }

      &::after {
        top: 6px;
        // left: 1px;
      }
    }
  }
}
